import AjaxRequest from "./ajax-request";

/**
 * Common fields js handler
 *
 * @package YITH WooCommerce Multi Vendor
 * @since 4.0.0
 */

export default class FieldsHandler {
	container = null;

	constructor( container ) {
		this.container = container;
	}

	init() {
		if ( ! this.container.length ) {
			return false;
		}

		// Init deps and fields.
		jQuery( document ).trigger( 'yith_fields_init' );
		jQuery( document.body ).trigger( 'yith-plugin-fw-init-radio' );
		jQuery( document ).trigger( 'yith-add-box-button-toggle' );

		// Init fields.
		this.initValue();
		this.initEnhancedSelect();
		// Init textarea editor.
		this.initTinyMCE();

		// Listen field change.
		this.container.on( 'change', '.field-required', {self: this}, this.listenRequired );
		this.container.on( 'change', '.email-validate', {self: this}, this.validateEmail );
		// Prevent submit on error.
		this.container.closest('form').on( 'submit', this.checkFormErrors.bind(this) );
	}

	initTinyMCE() {

		if ( typeof tinyMCE == 'undefined' || typeof tinyMCEPreInit == 'undefined' ) {
			return;
		}

		this.container.find( '.editor textarea' ).each( function() {
			// init editor
			let id = jQuery( this ).attr( 'id' ),
				mceInit = tinyMCEPreInit.mceInit,
				mceKey = Object.keys( mceInit )[0],
				mce = mceInit[mceKey],
				// get quick tags options
				qtInit = tinyMCEPreInit.qtInit,
				qtKey = Object.keys( qtInit )[0],
				qt = mceInit[qtKey];

			// change id
			mce.selector = id;
			mce.body_class = mce.body_class.replace( mceKey, id );
			qt.id = id;

			tinyMCE.init( mce );
			tinyMCE.execCommand( 'mceRemoveEditor', true, id );
			tinyMCE.execCommand( 'mceAddEditor', true, id );

			quicktags( qt );
			QTags._buttonsInit();
		} );
	}

	initValue() {
		// Init fields value.
		this.container.find( ':input' ).each( function() {
			let current = jQuery( this ),
				value = current.data( 'value' ) ?? null;

			if ( current.is( ':radio' ) || 'hidden' === current.attr( 'type' ) || null === value ) { // Radio is handled by plugin-fw
				return;
			}

			if ( current.is( ':checkbox' ) ) {
				let checked = current.is(':checked');
				if ( ( ! checked && 'yes' === value ) || ( checked && 'yes' !== value ) ) {
					current.click();
				}
			} else {
				current.val( value );
			}
		});
	}

	initEnhancedSelect() {
		// AjaxRequest module and selectWoo plugin are requested.
		if ( typeof yith_wcmv_ajax === 'undefined' || typeof jQuery.fn.selectWoo === 'undefined' ) {
			return false;
		}

		this.container
			.find( 'select.yith-wcmv-ajax-search' )
			.filter( ':not(.initialized)' )
			.each( function() {
				// Set value if any on data.
				let select = jQuery( this ),
					values = select.data( 'value' ) ?? null;

				if ( null !== values ) {
					for ( const option in values ) {
						select.append( new Option(values[option], option, true, true) );
					}
				}

				select.trigger('change');
				select.selectWoo( {
					allowClear: true,
					placeholder: jQuery( this ).data( 'placeholder' ),
					minimumInputLength: '3',
					escapeMarkup( m ) {
						return m;
					},
					ajax: {
						url: yith_wcmv_ajax.ajaxUrl,
						dataType: 'json',
						delay: 1000,
						data( params ) {
							return {
								term: params.term,
								request: jQuery( this ).data( 'action' ),
								action: yith_wcmv_ajax.ajaxAction,
								security: yith_wcmv_ajax.ajaxNonce,
								context: 'admin',
							};
						},
						processResults( results ) {
							let terms = [];
							if ( results.success ) {
								jQuery.each(
									results.data,
									function( id, text ) {
										terms.push( {
											id,
											text,
										} );
									}
								);
							}
							return {
								results: terms,
							};
						},
						cache: true,
					},
				} )
					.addClass( 'initialized' )
					.on( 'select2:select', function( event ) {
						select.find( 'option.value-placeholder' ).remove();
					})
					.on( 'select2:unselect', function( event ) {
						let unselected = event.params.data.id;
						select.find( 'option[value="' + unselected + '"]' ).remove();
						if ( ! select.find( 'option' ).length ) {
							select.append( '<option value="" class="value-placeholder"></option>' );
						}
					});
			} );

		// simple select!
		this.container
			.find( 'select' )
			.filter( ':not(.initialized)' )
			.each( function() {
				let select 		= jQuery( this ),
					value 		= select.data( 'value' ) ?? null,
					placeholder = jQuery( this ).find('option').filter( '[value=""]' ),
					args		= {
						minimumResultsForSearch: 20, // at least 20 results must be displayed
					};

				// Add placeholder if there is an empty option.
				if ( placeholder.length ) {
					args.placeholder =  {
						id: '', // the value of the option
						text: placeholder.text()
					};
					args.allowClear = true;
				}

				if ( null !== value ) {
					select.val( value ).change();
				}

				select.selectWoo( args ).addClass( 'initialized' );
			} );
	}

	isValidEmail( email ) {
		return email.toLowerCase().match(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);
	}

	validateEmail( event ) {
		const self = event.data.self;

		let input = jQuery( this ),
			value = input.val();

		if ( value && ! self.isValidEmail( value ) ) {
			self.addFieldError( yith_vendors?.emailFieldError, input.attr( 'name' ) );
		} else {
			self.resetFieldError( input.attr( 'name' ) );
		}
	}

	addFormError( error, wrap ) {
		wrap.prepend( '<div id="error-message">' + error + '</div>' );
	}

	addFieldError( error, field_name, wrap ) {
		if ( ! wrap || ! wrap.length ) {
			wrap = this.container;
		}

		let field =  wrap.find( '[name="' + field_name + '"]' );
		if ( ! field.length ) {
			this.addFormError( error, wrap );
		}

		let error_wrap = field.next( '.error-msg' );
		// Add error class.
		field.addClass( 'field-error' );
		// Add error.
		if ( error_wrap.length ) {
			error_wrap.html( error );
		} else {
			field.after(
				'<span class="error-msg">' + error + '</span>'
			);
		}
	}

	resetFormError( wrap ) {
		if ( ! wrap || ! wrap.length ) {
			wrap = this.container;
		}

		wrap.find( '#error-message' ).remove();
		// Reset single fields.
		wrap.find( '.field-error' ).each( ( i, field ) =>  {
			this.resetFieldError( jQuery( field ).attr('name') );
		});
	}

	resetFieldError( field_name ) {
		if ( field_name && this.container.find( '[name="' + field_name + '"]' ) ) {
			let field = this.container.find( '[name="' + field_name + '"]' );
			field.removeClass( 'field-error' );
			field.next( '.error-msg' ).remove();
		}
	}

	listenRequired( event ) {
		const self 	= event.data.self,
			field 	= jQuery(this),
			name    = field.attr('name');

		if ( ! field.val() ) {
			self.addFieldError( yith_vendors?.requiredFieldError, name );
		} else if ( ! field.hasClass( 'ajax-check' ) )  {
			self.resetFieldError( name );
		}
	}

	isFormWithErrors() {
		let form = this.container.closest('form');
		form.find(':input').filter('.field-required').trigger('change');
		return !! form.find('#error-message, .field-error').length;
	}

	checkFormErrors( event ) {
		if ( this.isFormWithErrors() ) {
			event.preventDefault();
			jQuery('html').animate({
				scrollTop: this.container.find('#error-message, .field-error').first().offset().top - 100
			}, 1000);
		}
	}
}
