/**
 * Shipping module JavaScript
 *
 * @since 4.0.0
 * @author Francesco Licandro
 * @package YITH WooCommerce Multi Vendor
 */

import AjaxRequest from './ajax-request';
import FieldsHandler from "./fields-handler";

class YITH_Shipping {
	zoneTable
	zoneAdd
	zoneList
	zoneListEmpty
	buttonAdd
	methodModal
	methodModalTrigger

	constructor() {
		this.zoneTable 			= jQuery( '#yith-wcmv-shipping-zones-wrapper' );
		this.zoneAdd 			= this.zoneTable.find( '.yith-wcmv-shipping-zone-new' );
		this.zoneList 			= this.zoneTable.find( '.yith-wcmv-shipping-zones' );
		this.zoneListEmpty		= this.zoneTable.find( '.yith-wcmv-shipping-zones-empty' );
		this.buttonAdd			= this.zoneTable.find( '.yith-wcmv-shipping-zones-add-button' );
		this.methodModal		= null;
		this.methodModalTrigger	= null;
		this.init()
	}

	init() {

		if ( ! this.zoneTable.length || typeof yith_wcmv_shipping_general === 'undefined' ) {
			return false;
		}

		// Init field.
		this._initSelect();
		// Ini sortable
		this._initSortable();
		// Listener actions.
		this.zoneTable.on( 'click', '.yith-wcmv-shipping-zone-select-action', this.selectRegionsActions );
		this.zoneTable.on( 'click', '.yith-wcmv-shipping-zone-postcodes-toggle', this.showRegionsPostcode );
		this.zoneTable.on( 'yith-wcmv-shipping-list-updated', this.handleListUpdate.bind(this) );

		// Handle Zone
		this.buttonAdd.on( 'click', this.addZone.bind(this) );
		this.zoneTable.on( 'click', '.edit-zone a', {self: this}, this.editZone );
		this.zoneTable.on( 'click', '.trash-zone a', {self: this}, this.deleteZone );
		this.zoneTable.on( 'click', '.save-zone', {self: this}, this.saveZone );

		// Handle Shipping Method
		this.zoneTable.on( 'click', '.yith-wcmv-shipping-zone-edit__method-add', {self: this}, this.addMethod );
		this.zoneTable.on( 'click', '.edit-method a', {self: this}, this.editMethod );
		this.zoneTable.on( 'click', '.trash-method a', {self: this}, this.deleteMethod );
		jQuery( document ).on( 'submit', '.yith-wcmv-shipping-method-modal form', {self: this}, this.modalSubmit );
	}

	// UTILS
	_zoneListIsEmpty() {
		return ! this.zoneList.children().length;
	}
	_switchButtonAdd( active ) {
		if ( active ) {
			this.buttonAdd.addClass('button-secondary').removeClass('button-primary').text( this.buttonAdd.data('closed_label') );
		} else {
			this.buttonAdd.addClass('button-primary').removeClass('button-secondary').text( this.buttonAdd.data('opened_label') );
		}
	}
	_initSelect( container ) {

		if ( typeof jQuery.fn.selectWoo === 'undefined' ) {
			return false;
		}

		if ( typeof container === 'undefined' ) {
			container = this.zoneTable;
		}

		jQuery( container ).find( 'select:not(.initialized)' ).each( function() {
			let args = {
					minimumResultsForSearch: 20 // at least 20 results must be displayed
				},
				value = jQuery( this ).data( 'value' ) ?? null;

			if ( jQuery( this ).hasClass( 'yith-wcmv-shipping-zone-region-select' ) ) {
				args.data = yith_wcmv_shipping_general.shippingRegions;
				args.escapeMarkup = function( m ) {
					return m;
				};
			}

			jQuery( this ).selectWoo( args ).addClass( 'initialized' );
			if ( value ) {
				jQuery( this ).val( value ).change();
			}
		} );
	}
	_initSortable() {
		let self		= this,
			items 		= this.zoneList.find( '.yith-wcmv-shipping-zone' ),
			initialized = this.zoneList.hasClass('ui-sortable');

		if ( items.length > 1 ) {
			if ( initialized ) {
				this.zoneList.sortable( 'refresh' );
			} else {
				this.zoneList.sortable({
					handle: '.drag-zone',
					cursor: 'move',
					scrollSensitivity: 10,
					tolerance: 'pointer',
					axis: 'y',
					stop: function(event, ui) {
						let order = [];
						self.zoneList.find( '.yith-wcmv-shipping-zone' ).each( function() {
							order.push( jQuery(this).data('zone_id' ) );
						});

						AjaxRequest.post(
							{
								request: yith_wcmv_shipping_general.orderZonesAction,
								order: order
							}
						);
					}
				});
			}
		}
	}

	// LISTENER
	handleListUpdate(){
		// Toggle empty list visibility.
		if ( this._zoneListIsEmpty() ) {
			this.zoneListEmpty.show();
		} else {
			this.zoneListEmpty.hide();
		}

		this._initSelect();
		this._initSortable();
	}
	showRegionsPostcode( event ) {
		event.preventDefault();
		event.stopImmediatePropagation();

		jQuery( this ).siblings( '.yith-wcmv-shipping-zone-postcodes' ).toggle();
	}
	selectRegionsActions( event ) {
		event.preventDefault();
		event.stopImmediatePropagation();


		const action = jQuery( this ).data( 'action' ),
			select = jQuery( this ).closest( '.yith-wcmv-shipping-zone-edit__field' ).find( '.yith-wcmv-shipping-zone-region-select' );

		if ( 'remove-all' === action ) {
			select.val( '' ).change();
		} else if ( 'select-all' === action ) {
			select.val( 'continent:all' ).change();
		}
	}

	// HANDLER ZONES
	addZone( event ) {
		event.preventDefault();
		event.stopImmediatePropagation();

		const template = wp.template( 'yith-wcmv-shipping-zones-add' );

		if ( this.zoneAdd.is(':visible' ) ) {
			this._switchButtonAdd( false );
			this.zoneAdd.slideToggle( '400', () => {this.zoneAdd.html('');});
		} else {
			this._switchButtonAdd( true );
			this.zoneAdd.html( template( {} ) );
			this._initSelect();
			// At the end, show it!
			this.zoneAdd.slideToggle();
		}
	}
	editZone( event ) {
		event.preventDefault();
		const wrap = jQuery(this).closest( '.yith-wcmv-shipping-zone' );

		wrap.toggleClass( 'editing' );
		// Handle container class.
		if ( ! wrap.siblings( '.editing' ).length ) {
			event.data.self.zoneList.toggleClass( 'sortable-disabled' );
		}
		// Show edit form.
		wrap.find( '.yith-wcmv-shipping-zone-edit' ).slideToggle();
	}
	deleteZone( event ) {
		event.preventDefault();
		event.stopImmediatePropagation();

		const self = event.data.self,
			zone = jQuery( this ).closest( '.yith-wcmv-shipping-zone' ),
			zone_id = zone.data( 'zone_id' );

		yith.ui.confirm(
			{
				title            : yith_wcmv_shipping_general.removeZoneTitle,
				message          : yith_wcmv_shipping_general.removeZoneMessage,
				confirmButtonType: 'delete',
				confirmButton    : yith_wcmv_shipping_general.removeButtonLabel,
				closeAfterConfirm: true,
				onConfirm        : function () {
					zone.remove();
					self.zoneTable.trigger( 'yith-wcmv-shipping-list-updated' );

					AjaxRequest.post(
						{
							request: yith_wcmv_shipping_general.removeZoneAction,
							zone_id: zone_id,
						},
					);
				},
			}
		);
	}
	saveZone( event ) {
		event.preventDefault();
		event.stopImmediatePropagation();

		const self = event.data.self,
			zone 	= jQuery( this ).closest( '.yith-wcmv-shipping-zone, .yith-wcmv-shipping-zone-new' ),
			zone_id = zone.data( 'zone_id' );

		let data = zone.find(':input').serializeArray().filter( (field) => {
			return field.value.length;
		});
		if ( ! data.length ) {
			return false;
		}

		data.push(
			{name: "request", value: yith_wcmv_shipping_general.saveZoneAction },
			{name: "zone_id", value: zone_id }
		);

		AjaxRequest.post( data, zone )
			.done( ( res ) => {

				if ( res?.success && res?.data?.html ) {
					let to_replace = self.zoneTable.find( '.yith-wcmv-shipping-zone[data-zone_id="' + res?.data?.zone_id + '"]' );

					if ( to_replace.length ) {
						to_replace.replaceWith( res.data.html );
					} else {
						// Reset form.
						self._switchButtonAdd( false );
						self.zoneAdd.html('').hide();
						// Add zone.
						self.zoneList.append( res.data.html );
					}

					self.zoneTable.trigger( 'yith-wcmv-shipping-list-updated' );
				}
			} );
	}

	// HANDLER METHODS
	openModal( title, content, width ) {
		const self = this;

		self.methodModal = yith.ui.modal( {
			width: width ?? 500,
			classes: {
				wrap: 'yith-wcmv-shipping-method-modal',
				content: 'yith-plugin-ui',
			},
			title: title,
			content: content,
			onCreate() {
				const fields = new FieldsHandler( jQuery( '.yith-wcmv-shipping-method-modal' ) );
				fields.init();
			}
		} );
	}
	addMethod( event ) {
		event.preventDefault();
		event.stopImmediatePropagation();

		const self = event.data.self,
			template = wp.template( 'yith-wcmv-shipping-zones-add-method' );

		self.openModal( yith_wcmv_shipping_general.shippingMethodTitle, template( {zone_id: jQuery(this).data( 'zone_id' ) } ) );
	}
	editMethod( event ) {
		event.preventDefault();
		event.stopImmediatePropagation();

		const self = event.data.self,
			trigger = jQuery(this).parent(),
			method_id = trigger.attr( 'data-method_id' ),
			template = wp.template( 'yith-wcmv-shipping-zones-edit-method' );

		let data = JSON.parse( jQuery( '#yith-wcmv-shipping-method-data-' + method_id ).val() );
		// Add keys to data.
		data.zone_id = trigger.attr( 'data-zone_id' );
		data.method_id = method_id;

		self.openModal(
			yith_wcmv_shipping_general.shippingEditMethodTitle.replace( '{{method_title}}', data.method_title ),
			template( data ),
			600
		);
	}
	deleteMethod( event ) {
		event.preventDefault();
		event.stopImmediatePropagation();

		const method 		= jQuery( this ).closest( '.yith-wcmv-shipping-zone-edit__method' ),
			trigger 	= jQuery(this).parent(),
			method_id 	= trigger.attr( 'data-method_id' ),
			zone_id 	= trigger.attr( 'data-zone_id' );

		yith.ui.confirm(
			{
				title            : yith_wcmv_shipping_general.removeMethodTitle,
				message          : yith_wcmv_shipping_general.removeMethodMessage,
				confirmButtonType: 'delete',
				confirmButton    : yith_wcmv_shipping_general.removeButtonLabel,
				closeAfterConfirm: true,
				onConfirm        : function () {
					method.remove();
					AjaxRequest.post(
						{
							request: yith_wcmv_shipping_general.removeShippingMethod,
							method_id: method_id,
							zone_id: zone_id,
						},
					);
				},
			}
		);
	}
	modalSubmit( event ) {
		event.preventDefault();
		event.stopImmediatePropagation();

		const self = event.data.self,
			form = jQuery( this ),
			data = form.serializeArray();

		AjaxRequest.post( data, jQuery('.yith-plugin-fw__modal__main') )
			.done( ( res ) => {

				if ( res?.success && res?.data?.html ) {
					let zone = self.zoneTable.find( '.yith-wcmv-shipping-zone[data-zone_id="' + res?.data?.zone_id + '"]' );
					if ( ! zone.length && self.zoneAdd.is(':visible') ) {
						zone = self.zoneAdd;
					}

					let method = zone.find( '.yith-wcmv-shipping-zone-edit__method[data-method_id="' + res?.data?.method_id + '"]' );
					if ( method.length ) {
						method.replaceWith( res.data.html );
					} else {
						zone.find( '.yith-wcmv-shipping-zone-edit__methods ul' ).prepend( res.data.html );
					}

					// Close modal
					self.methodModal.close();
				}
			} );
	}
}

const shippingTable = new YITH_Shipping();
